import React from "react";
import "./about.css";
import Image from "../../assets/man.png";
import PdfCv from "../../private_files/CV_Dominik_Such.pdf";

const About = () => {
  return (
    <section className="about container section" id="about">
      <h2 className="section__title">O mnie</h2>
      <div className="about__container grid">
        <img src={Image} alt="" className="about__img" />
        <div className="about__data">
          <div className="about__info">
            <p className="about__description">
              Nazywam si Dominik Such. Moim hobby jest testowanie oraz nauka
              programowania. Od okoo roku pracuj jako gwny tester aplikacji
              mobilnych, oraz webowych gdzie zajmuj si testowaniem manualnym.
              W wolnym czasie ucz si programowania testw automatycznych,
              ktre zaczem wykorzystywa w swojej pracy, gwnie przy uyciu
              narzdzia Cypress, a take programowania w jzyku JavaScript
              wykorzystujc bibliotek React. Poszukuj miejsca pracy, ktre
              zapewni mi moliwo dalszego rozwoju.
            </p>
            <a href={PdfCv} className="btn" download="CV_Dominik_Such.pdf">
              Pobierz CV
            </a>
          </div>
        </div>
      </div>
    </section>
  );
};

export default About;
